.bg{
    background: url(../images/banner.png) no-repeat;
    background-size: cover;
    .title{
        height: 840px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;
        color: #650300;
    }
}

.bg-2{
    background: #AA06010D no-repeat;
}

.Promise{
    height: 481px;
    padding-top: 60px;
    padding-bottom: 80px;
    h2{
        text-align: center;
        color:#650300;
    }
    ul{
        margin-top: 40px;
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        li{
            max-width: 24%;
            img{
                margin: 0 auto;
            }
            h4{
                color:#650300;
                text-align: center;
                margin-top: 16px;
                margin-bottom: 8px;
            }
            p{
                text-align: left;
            }
        }
    }
}

.Frame{
    text-align: center;
    padding-top: 60px;
    padding-bottom: 80px;
    h2{
        margin-bottom: 8px;
        color:#650300;
    }
    li{
        display: flex;
        align-items: center;
        margin-top: 32px;
        h2{
            color:#AA0601;
            
        }
        
    }
    span{
        color:#AA0601;
        font-size: 48px;
        margin-right: 8px;
    }
    .right{
        justify-content: flex-end;
        img{
            margin-left: 30px;
        }
    }
    .left{
        img{
            margin-right: 30px;
        }
    }
}

.bg-3{
    background: url(../images/bg.png) no-repeat center;
    background-size: cover;
    height: 705px;
    padding-top: 60px;
    padding-bottom: 80px;
    .branding{
        text-align: center;
        h2{
            color: #FFFFFF;
            margin-bottom: 40px;
        }
        ul{
            display: flex;
            justify-content: space-between;
            li{
                width: 540px;
                height: 455px;
                display: flex;
                justify-content: center;
                align-items: center;
                p{
                    @include fz-lh-col (72px, #AA0601)
                }
            }
            .box1{
                background: url(../images/product-8.png) rgba(255, 255, 255, .5) no-repeat center center;
                background-blend-mode: lighten;
                background-size: cover;
            }
            .box2{
                background: url(../images/product-9.png) rgba(255, 255, 255, .5) no-repeat center center;
                background-blend-mode: lighten;
                background-size: cover;
            }
        }
    }
}

.customer{
    padding-top: 60px;
    padding-bottom: 80px;
    h2{
        text-align: center;
    }
    ul{
        display: flex;
        justify-content: space-between;
        li{
            max-width: 255px;
            height: 460px;
            border: 1px solid #DEE2E6;
            box-shadow:0px 2px 6px #00000029;
            .content{
                height: 204px;
                padding: 16px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                
                // .name, .time{
                //     @include fz-lh-col (14px, 21px, #000000)
                // }
                // .time{
                //     color: #707070;
                //     display: flex;
                //     align-items: baseline;
                // }
            }
        }
    }
}

.contact{
    max-width: 730px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    padding-top: 60px;
    padding-bottom: 80px;
    h2{
        text-align: center;
    }
    p{
        font-size: 20px;
        line-height: 30px;
        margin-bottom: 20px;
    }
    label{
        font-size: 16px;
        line-height: 24px;
    }
    input{
        height: 50px;
        background: #AA06010D;
        opacity: initial;
        margin-bottom: 16px;
        padding-left: 1rem;
    }
    ::placeholder{
        font-size: 16px;
        line-height: 24px;
    }
    input[id=feedback]{
        height: 136px;
        ::placeholder{
            
        }
    }

    .check{
        display: flex;
        align-items: center;

        p{
            margin-left: 4px;
            font-size: 16px;
            line-height: 24px;
        }
    }
    button{
        width: 350px;
        height: 54px;
        margin: 0 auto;
        background: #AA0601;
        color: #FFFFFF;
        font-size: 16px;
    }
}

.choice{
    display: flex;
        li{
            width: 33%;
        }
        a{
            height: 96px;
            font-size: 32px;
            border: 1px solid #DCDCDC;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #000;
            span{
                font-size: 44px;
            }
            &:hover{
                border-bottom: 8px solid #AA0601;
            }
        }
    
}

.bg-4{
    display: flex;
}

.Celluloid{
    text-align: center;
    padding-top: 80px;
    padding-bottom: 160px;
    ul{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 40px;
        li{
            width: 23%;
            padding-bottom: 104px;
            .ntd{
                display: flex;
                justify-content: space-between;
                .price{
                    color: #AA0601;
                }
            }
            .color{
                display: flex;
                span{
                    width: 24px;
                    height: 24px;
                    margin-right: 8px;
                    border-radius:999em;
                }
                .brown{
                    background: #5F3E2D;
                }
                .orange{
                    background: #B75929;
                }
            }
            
        }
    }
}

.next{
    display: flex;
    justify-content: center;
    a{
        color: #555555;
        border: 1px solid #555555;
        font-size: 18px;
        padding: 12px 16px;
        .page{
            font-size: 19px;
            padding: 11px 12px;
            background: #F2F2F2;
        }
        &:hover{
            background: #000000;
            color: #FFFFFF;
        }
    }
}

