.shop{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-bottom: 160px;
    li{
        width: 32%;
        margin-top: 32px;
        h4{
            border: 1px solid #00000020;
            padding: 12px 24px;
        }
        h5{
            text-align: center;
            padding: 20px 0;
            border: 1px solid #000;
            background: #000;
            color: #FFFFFF;
        }
        a{
            :hover{
                background: #CED4DA;
                color: #000;
                border: 1px solid #CED4DA;
            }
        }
        .site{
            padding: 16px 26px;
            border: 1px solid #00000020;
            align-items: center;
            p{
                font-size: 20px;
                display: flex;
                align-items: center;
                
            }
            .up{
                align-items: flex-start;
            }
            span{
                font-size: 20px;
                margin-right: 10px;
            }
        }
    }
}


