        font-size: 30px;   
    }
}

.bolg{
    padding-top: 96px;
    padding-bottom: 160px;
    h2{
        border-left: 8px solid #9F1213;
        padding-left: 16px;
        margin-bottom: 65px;
    }
    ul{
        display: flex;
        flex-wrap: wrap;
        li{
            
            a{
                display: flex;
                justify-content: space-between;
                margin-bottom: 60px;
            }
            .set{
                max-width: 45%;
                h4{
                    font-size: 32px;
                }
                h3{
                    text-align:end;
                }
                h4, h3{
                    color: #650300;
                }
                h5{
                    color: #000;
                    display: flex;
                    justify-content: space-between;
                    span{
                        color: #555555;
                    }
                }
                p{
                    color: #555555;
                    font-size: 20px;
                    margin-top: 14px;
                    margin-bottom: 24px;
                }

            }
        }
    }
