.detail{
    p{
        margin: 8px 0;
        &::after{
            content: '情人特別企劃';
            color: #AA0601;
        }
    }
    span{
        font-size: 24px;
        color: #AA0601;
        margin-right: 16px;
    }
}


.article{
    display: flex;
    justify-content: space-between;
    .event{
        width: 75%;
        padding-bottom: 160px;
        h2{
            font-size: 48px;
        }
        h4{
            font-size: 28px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            span{
                font-size: 16px;
            }
        }
        img{
            margin: 24px 0;
        }
        p{
            font-size: 20px;
            color: #0A0A0A;
            padding-bottom: 16px;
        }
        .share{
            p{
                color: #0A0A0A;
                margin-bottom: 4px;
                margin-top: 24px;
            }
            svg{
                margin-left: 16px;
            }
            .arrow{
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-top: 1px solid #707070;
                p{
                    font-size: 19px;
                }
                span{
                    font-size: 16px;
                }
            }
        }
    }
    .tags{
        width: 23%;
        color: #AA0601;
        h4{
            font-size: 28px;
            display: flex;
            align-items: center;
            span{
                font-size: 38px;
                margin-right: 12px;
            }
        }
        span{
            font-size: 24px;
            margin: 8px 0;
            margin-right: 24px;
        }
    }
}

