body{
    line-height: 1.5;
    font-family: 'Roboto', sans-serif;
}
a{
    text-decoration: none;
}
img{
    display: block;
}

h1{
    font-size: 64px;
}
h2{
    font-size: 48px;
}
h3{
    font-size: 36px;
}
h4{
    font-size: 24px;
}
h5{
    font-size: 19px;
}
p{
    font-size: 16px;
}


.container{
    max-width: 1110px;
    margin: 0 auto;
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #AA0601;
    padding: 30px 88px;
    ul{
        display: flex;
        flex-wrap: wrap;
        li{
            margin-left: 64px;
            @include fz-lh-col (20px, #FFFFFF);
        }
    }
}

.footer{
    background: #AA0601;
    padding-top: 32px;
    .top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include fz-lh-col (16px, #FFFFFF);
        ul{
            display: flex;
            color: #FFFFFF;
        }
        .menu li{
                margin-right: 48px;
            }
        .link li{
            margin-left: 32px;
        }
    }
    .middle{
        margin-top: 36px;
        padding-bottom: 8px;
        border-bottom: 1px solid #FFFFFF;
        h3{
            display: flex;
            align-items: center;
            color: #FFFFFF;
            margin-bottom: 16px;
            span{
                font-size: 36px;
                margin-right: 20px;
            }
        }
        
    }
    .matter{
        display: flex;
        justify-content: space-between;
        padding-top: 32px;
        padding-bottom: 39px;
        p{
            @include fz-lh-col (16px, #FFFFFF);
        }
        ul{
            display: flex;
            li{
                @include fz-lh-col (14px, #FFFFFF);
                margin-left: 48px;
            }
        }
    }

}
